import React from "react"
import Layout2 from "../components/layout2"
import SEO from "../components/seo"
import Footer from "../components/footer"

import Waves from "../images/wave.png"
import Euryale from "../images/lore/Euryale.png"
import Galanthus from "../images/lore/Galanthus.png"
import Helia from "../images/lore/Helia.png"

const Lore = () => {
  const charms = [5, 5, 5, 5, 5]

  function genCharmCard(charm, i) {
    return <div className="card" key={i}>x</div>
  }

  return (
    <>
      <Layout2>
        <SEO title="lore" />
        <div className="charmBack">
          <img className="water" src={Waves} width="664" height="564" alt="water" />
          <img className="fire" src={Waves} width="664" height="564" alt="fire" />
        </div>
        <div className="charmGrid">
          {charms.map((charm, i) => genCharmCard(charm, i))}
        </div>
        <div className="heroText">
          <h3> Wiki </h3>
          <p> The elements, at your fingertips </p>
          <h1> Under construction </h1>
        </div>
        <div className="charmGrid">
          {charms.map((charm, i) => genCharmCard(charm, i))}
        </div>
      </Layout2>
      <div className="pageContent">
        <div className="row">
          <div className="card empty pop">
            <h3> Character Concepts </h3>
            <h1> Under construction </h1>
          </div>
          <div className="character pop">
            <h2>Helia</h2>
            <div className="card">
              <img className="portrait" src={Helia} alt="Helia" />
            </div>
          </div>
          <div className="character pop">
            <h2>Euryale</h2>
            <div className="card">
              <img className="portrait" src={Euryale} alt="Euryale" />
            </div>
          </div>
          <div className="character pop">
            <h2>Galanthus</h2>
            <div className="card pop">
              <img className="portrait" src={Galanthus} alt="Galanthus" />
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </>
  )
}

export default Lore